export default {
	data() {
		return {
			searchVal:"",
			tabCurrent: 0,
			priceSort: 0,
			theCity: '北京',
			status: 'loading',
			citys: [{
				name: '武汉'
			}, {
				name: '广州'
			}, {
				name: '北京'
			}, {
				name: '乌鲁木齐乌鲁木齐'
			}],
			index: 0,
			bannerList: [{
					colorClass: 'uni-bg-red',
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/shuijiao.jpg',
					content: '内容 A'
				},
				{
					colorClass: 'uni-bg-green',
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/muwu.jpg',
					content: '内容 B'
				},
				{
					colorClass: 'uni-bg-blue',
					url: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/cbd.jpg',
					content: '内容 C'
				}
			],
			themeColor: '#007AFF',
			mode: '',
			deepLength: 1,
			pickerValueDefault: [0]
		}
	},
	created() {
		const that = this;
	},
	methods: {
		bindPickerChange: function(e) {
			let that = this;
			that.index = e.detail.value;
			that.theCity = that.citys[e.detail.value].name;
		},
		selectTab(index) {
			let that = this;
			if (index == 3 && that.tabCurrent == 3) {
				that.priceSort = that.priceSort == 0 ? 1 : 0
			} else {
				that.tabCurrent = index;
			}
		},
		itemTap() {
			this.$Router.push({
				path:"/pages/home/goodsDetail"
			})
		},
		onReachBottom() {
			console.log("onReachBottom");
		},
		search(res) {
			let that = this;
			that.$Router.push({
				path:"/pages/home/searchDetail",
				query:{
					text:that.searchVal
				}
			})
			uni.hideKeyboard();
		},
		gotoAreaHousing(){
			this.$Router.push({
				path:"/pages/home/areaHousing",
				query:{
					aDs:"11122",
					fds:12321,
					vcds:"dsads"
				}
			})
		},
		gotoLeakHousing(){
			this.$Router.push({
				path:"/pages/home/leakHousing"
			})
		}
		
	}
}
